<template>
  <div class="match-box">
    <template v-for="(day, index) in liveSportsItemsList">
      <!-- sportsCategoryId (integer, optional): Id ,
          sportsCategoryName (string, optional):  ,
          leftGoal (string, optional):  ,
          rightGoal (string, optional):  ,
          leftBadgeUrl (string, optional): Url ,
          rightBadgeUrl (string, optional): Url ,
          leftScore (integer, optional):  ,
          rightScore (integer, optional):  ,
          categoryAlias (string, optional): ,NBA ,
          startTime (string, optional):  ,
          status (integer, optional): ,0 :  1:  2: = ['0', '1', '2'],
          statusName (string, optional, read only):  ,
          hasVideo (boolean, optional): [] ,
          hasHighlights (boolean, optional): [] ,
          id (integer, optional) -->
      <div class="title-data" :key="index">{{day.monthDay}}  {{day.dayName}}</div>
      <div class="match-list clearfix" v-for="(item) in day.list" :key="item.id">
          <div class="pull-left match-time">
              <span>{{item.startTimeHHmm}}</span>
          </div>
          <div class="pull-left match-name text-hidden">
              <span>{{item.categoryAlias}}</span>
          </div>
          <div class="pull-left match-team home-team text-right">
              <span>{{item.leftGoal}}</span>
              <img :src="item.leftBadgeUrl" width="28" height="31" alt="">
          </div>
          <div class="pull-left match-vs strong">VS</div>
          <div class="pull-left match-team away-team text-left">
              <img :src="item.rightBadgeUrl" width="32" height="32" alt="">
              <span>{{item.rightGoal}}</span>
          </div>
          <div class="pull-right match-live-btn">
              <a href="javascript:;"
                :class="[item.status == 1 ? 'live-btn' : 'default-btn']"
                @click="navigateTo(0, item.id, item.status)">{{item.statusName}}
              </a>
          </div>
      </div>
    </template>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { formatToMonthDayZh, formatToyyyyMMdd, formatToHHmm } from '@/utils'
export default {
  data () {
    return {
      activeIndex: 0
    }
  },
  computed: {
    ...mapGetters({
      liveSportsItems: 'liveSportsItems'
    }),
    liveSportsItemsList () {
      let list = this.liveSportsItems
      let result = {}
      let today = formatToyyyyMMdd(new Date())
      let tomorrow = formatToyyyyMMdd(new Date(new Date().getTime() + 8.64e7))
      let afterTomorrow = formatToyyyyMMdd(new Date(new Date().getTime() + 1.728e8))
      list.forEach(item => {
        item.startTimeHHmm = formatToHHmm(item.startTime)
        if (today == formatToyyyyMMdd(item.startTime)) {
          result['today'] || (result['today'] = {
            monthDay: '',
            dayName: '',
            list: []
          })
          result['today'].monthDay = formatToMonthDayZh(item.startTime)
          result['today'].dayName = ''
          result['today'].list.push(item)
        } else if (tomorrow == formatToyyyyMMdd(item.startTime)) {
          result['tomorrow'] || (result['tomorrow'] = {
            monthDay: '',
            dayName: '',
            list: []
          })
          result['tomorrow'].monthDay = formatToMonthDayZh(item.startTime)
          result['tomorrow'].dayName = ''
          result['tomorrow'].list.push(item)
        } else if (afterTomorrow == formatToyyyyMMdd(item.startTime)) {
          result['afterTomorrow'] || (result['afterTomorrow'] = {
            monthDay: '',
            dayName: '',
            list: []
          })
          result['afterTomorrow'].monthDay = formatToMonthDayZh(item.startTime)
          result['afterTomorrow'].dayName = ''
          result['afterTomorrow'].list.push(item)
        }
      })
      return result
    }
  },
  methods: {
    navigateTo (videoCategory, videoId, state) {
      if (state != '1') return // 
      // videoCategory , 0: 1: 2: 99:
      this.$router.push({ name: 'live', params: { videoCategory: videoCategory, videoId: videoId } })
    }
  }
}
</script>

<style>

</style>



