<template>
  <div class="article-box">
      <!-- title (string, optional):  ,
        source (string, optional):  ,
        content (string, optional):  ,
        publishTime (string, optional):  ,
        id (integer, optional) -->
      <h1 class="title">{{article.title}}</h1>
      <span class="text-999 fs12">{{article.date}}   :{{article.source}}</span>
      <div class="article-info" v-html="article.content"></div>
      <div class="announce">A8</div>
      <div class="ban-box">
          <span class="tag"></span>
          <span class="tag"> <a href="javascript:;" target="_blank"></a></span>
          <span class="tag"> <a href="javascript:;" target="_blank"></a></span>
          <span class="tag"> <a href="javascript:;" target="_blank"></a></span>
          <span class="tag"> <a href="javascript:;" target="_blank"></a></span>
      </div>
      <div class="share-box">
          <span class="tag"></span>
      </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { formatToyyyyMMdd8 } from '@/utils'
export default {
  computed: {
    ...mapGetters({
      sportsNewsContent: 'sportsNewsContent'
    }),
    article () {
      let article = this.sportsNewsContent
      if (!article.title) return {}
      article.date = formatToyyyyMMdd8(article.publishTime)
      return article
    }
  }
}
</script>

<style>

</style>



