<template>
      <div class="content">
        <div class="fan-com">
            <div class="clearfix fan-header">
                <h2 class="pull-left"> <i class="icon-fan"></i></h2>
                <ul class="pull-left fan-nav">
                    <li v-for="(item, index) in fanCommunityCategories"
                      :key="item.id"
                      :class="[activeIndex == index ? 'active' : '']"
                      @click="changeFanCommunityCategory(item.id, index)">
                      <a href="javascript:;">{{item.cnName}}</a>
                    </li>
                </ul>
            </div>
            <div class="fan-list">
                <ul class="clearfix">
                    <li class="item" v-for="item in fanCommunityAd" :key="item">
                        <div class="fan-item">
                            <div class="win-rate">100%</div>
                            <div class="clearfix fan-info">
                                <div class="pull-left face-img"><img alt="" title="" src="../../static/images/icon-football.png"></div>
                                <div class="pull-left fan-name">
                                    <p class="fs16"></p>
                                    <span>109</span><span>10</span>
                                </div>
                            </div>
                            <p class="fan-special"></p>
                            <div class="fan-group clearfix">
                                <ul class="pull-left">
                                    <li><img src="../../static/images/icon-football.png" width="20" height="20" alt="">356564673</li>
                                    <li><img src="../../static/images/icon-football.png" width="20" height="20" alt="">356564673</li>
                                </ul>
                                <div class="pull-right group-ewm">
                                    <img src="../../static/images/wexin-group.jpg" alt="">
                                </div>
                            </div>
                            <!-- TODO:  -->
                            <!-- adImageUrl (string, optional): url ,
                              adUrl (string, optional): Url ,
                              qrCodeUrl (string, optional): Url ,
                              id (integer, optional)
                              ======================
                              {
                                "adImageUrl": "http://www.qq.com",
                                "adUrl": "http://www.baidu.com",
                                "qrCodeUrl": "http://www.google.com",
                                "id": 1
                              }-->
                            <div class="cover-vague" v-if="item.empty">
                                <a href="javascript:;" class="rec-coop"></a>
                                <p></p>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data () {
    return {
      activeIndex: 0
    }
  },
  computed: {
    ...mapGetters({
      fanCommunityCategories: 'fanCommunityCategories',
      fanCommunityAd: 'fanCommunityAd'
    })
  },
  methods: {
    ...mapActions([
      'setFanCommunityAdByCategory'
    ]),
    changeFanCommunityCategory (id, index) {
      this.activeIndex = index
      this.setFanCommunityAdByCategory(id)
    }
  }
}
</script>

<style>

</style>



