<template>
  <div class="main-box clearfix">
    <div class="pull-left main-box-left">
        <tab-nav-red></tab-nav-red>
        <div class="match-box">
          <!-- {
                "sportsCategoryId": 14,
                "sportsCategoryName": "NBA",
                "leftGoal": "",
                "rightGoal": "",
                "leftBadgeUrl": "https://leqiuba.oss-cn-shenzhen.aliyuncs.com/Uploads/2018-04-03/5ac3276b2a112.png",
                "rightBadgeUrl": "https://leqiuba.oss-cn-shenzhen.aliyuncs.com/Uploads/2018-04-03/5ac3273f8d98c.png",
                "leftScore": 116,
                "rightScore": 108,
                "categoryAlias": "NBA",
                "startTime": "2018-12-11T11:30:00",
                "status": 2,
                "statusName": "",
                "hasVideo": true,
                "hasHighlights": true,
                "id": 27
              }
              sportsCategoryId (integer, optional): Id ,
              sportsCategoryName (string, optional):  ,
              leftGoal (string, optional):  ,
              rightGoal (string, optional):  ,
              leftBadgeUrl (string, optional): Url ,
              rightBadgeUrl (string, optional): Url ,
              leftScore (integer, optional):  ,
              rightScore (integer, optional):  ,
              categoryAlias (string, optional): ,NBA ,
              startTime (string, optional):  ,
              status (integer, optional): ,0 :  1:  2: = ['0', '1', '2'],
              statusName (string, optional, read only):  ,
              hasVideo (boolean, optional): [] ,
              hasHighlights (boolean, optional): [] ,
              id (integer, optional) -->
            <div class="match-list clearfix" v-for="(item) in highlightsSportsItemList" :key="item.id">
                <div class="pull-left match-time">
                    <span>{{item.MMdd}}</span>
                    <span>{{item.HHmm}}</span>
                </div>
                <div class="pull-left match-name text-hidden">
                    <span>{{item.categoryAlias}}</span>
                </div>
                <div class="pull-left match-team home-team text-right">
                    <span>{{item.leftGoal}}</span>
                    <img :src="item.leftBadgeUrl ? item.leftBadgeUrl : '../../static/images/home-logo.png'" width="28" height="31" alt="">
                </div>
                <div class="pull-left match-vs strong">VS</div>
                <div class="pull-left match-team away-team text-left">
                    <img :src="item.rightBadgeUrl ? item.rightBadgeUrl : '../../static/images/away-logo.png'" width="32" height="32" alt="">
                    <span>{{item.rightGoal}}</span>
                </div>
                <div class="pull-right match-live-btn" v-if="item.hasVideo">
                    <a href="javascript:;" class="video-btn" @click="navigateTo(1, item.id)"></a>
                </div>
                <div class="pull-right match-live-btn" v-if="item.hasHighlights">
                    <a href="javascript:;" class="collect-btn" @click="navigateTo(2, item.id)"></a>
                </div>
            </div>
            <pagination></pagination>
        </div>
    </div>
    <div class="pull-right main-box-right">
        <qrcode></qrcode>
    </div>
</div>
</template>

<script>
import { mapGetters } from 'vuex'
import { formatToMonthDay, formatToHHmm } from '@/utils'
import tabNavRed from '@/components/public/tabNavRed'
import QRCode from '@/components/public/QRCode'
import pagination from '@/components/public/pagination'
export default {
  components: {
    'tab-nav-red': tabNavRed,
    'pagination': pagination,
    'qrcode': QRCode
  },
  computed: {
    ...mapGetters({
      highlightsSportsItem: 'highlightsSportsItem'
    }),
    highlightsSportsItemList () {
      let list = this.highlightsSportsItem
      list.forEach(x => {
        x.MMdd = formatToMonthDay(x.startTime)
        x.HHmm = formatToHHmm(x.startTime)
      })
      return list
    }
  },
  methods: {
    navigateTo (videoCategory, videoId) {
      // videoCategory , 0: 1: 2: 99:
      this.$router.push({ name: 'live', params: { videoCategory: videoCategory, videoId: videoId } })
    }
  }
}
</script>

<style>

</style>



