// axios
import axios from 'axios'

axios.defaults.baseURL = 'http://47.107.158.118:8080'
// 
axios.defaults.headers = {
  'X-Requested-With': 'XMLHttpRequest',
  'Content-Type': 'application/json;charset=UTF-8'
}
axios.defaults.timeout = 10000

let cancel
let promiseArr = {}
const CancelToken = axios.CancelToken
// 
axios.interceptors.request.use(config => {
  // 
  if (promiseArr[config.url]) {
    promiseArr[config.url]('')
    promiseArr[config.url] = cancel
  } else {
    promiseArr[config.url] = cancel
  }
  return config
}, error => {
  return Promise.reject(error)
})

// 
axios.interceptors.response.use(response => {
  return response
}, err => {
  if (err && err.response) {
    switch (err.response.status) {
      case 400:
        err.message = ''
        break
      case 401:
        err.message = ''
        break
      case 403:
        err.message = ''
        break
      case 404:
        err.message = ','
        break
      case 405:
        err.message = ''
        break
      case 408:
        err.message = ''
        break
      case 500:
        err.message = ''
        break
      case 501:
        err.message = ''
        break
      case 502:
        err.message = ''
        break
      case 503:
        err.message = ''
        break
      case 504:
        err.message = ''
        break
      case 505:
        err.message = 'http'
        break
      default:
        err.message = `${err.response.status}`
    }
  } else {
    err.message = ''
  }
  // message.err(err.message)
  console.log(err.message)
  return Promise.resolve(err.response)
})

// axios.defaults.baseURL = '/api'
// // 
// axios.defaults.headers = {
//   'X-Requested-With': 'XMLHttpRequest'
// }
// axios.defaults.timeout = 10000

export default {
  // get
  get (url, param) {
    return new Promise((resolve, reject) => {
      axios({
        method: 'get',
        url,
        params: param,
        cancelToken: new CancelToken(c => {
          cancel = c
        })
      }).then(res => {
        resolve(res)
      })
    })
  },
  // post
  post (url, param, config) {
    config = config || {}
    config.headers = Object.assign({}, {
      'X-Requested-With': 'XMLHttpRequest',
      'Content-Type': 'application/json;charset=UTF-8'
    }, config.headers)
    return new Promise((resolve, reject) => {
      axios({
        method: 'post',
        url,
        headers: config.headers || { 'X-Requested-With': 'XMLHttpRequest' },
        data: JSON.stringify(param),
        cancelToken: new CancelToken(c => {
          cancel = c
        })
      }).then(res => {
        resolve(res)
      })
    })
  }
}



